import React, { useMemo } from 'react';
import Page from '../../components/Page';
import { createGlobalStyle } from 'styled-components';
import CountUp from 'react-countup';
import CardIcon from '../../components/CardIcon';
import TokenSymbol from '../../components/TokenSymbol';
import useBnbmStats from '../../hooks/useBnbmStats';
import useLpStats from '../../hooks/useLpStats';
import useLpStatsBTC from '../../hooks/useLpStatsBTC';
import useModal from '../../hooks/useModal';
import useZap from '../../hooks/useZap';
import useBondStats from '../../hooks/useBondStats';
import useSBnbStats from '../../hooks/useSBnbStats';
import useTotalValueLocked from '../../hooks/useTotalValueLocked';
// import { Bnbm as bnbmTesting, SBnb as carTesting } from '../../bnbm-finance/deployments/deployments.testing.json';
import { Bnbm , SBnb } from '../../bnbm-finance/deployments/deployments.mainnet.json';
import { roundAndFormatNumber } from '../../0x';
import MetamaskFox from '../../assets/img/metamask-fox.svg';
import { Box, Button, Card, CardContent, Grid, Paper, Typography } from '@material-ui/core';
import ZapModal from '../Bank/components/ZapModal';
import BNBM_BNBZapModal from '../Bank/components/BNBM_BNBZapModal';
import { Alert } from '@material-ui/lab';
import { makeStyles } from '@material-ui/core/styles';
import useBnbmFinance from '../../hooks/useBnbmFinance';
import { ReactComponent as IconTelegram } from '../../assets/img/telegram.svg';
import { Helmet } from 'react-helmet'
import BnbmImage from '../../assets/img/bnbmLogo.svg';
import kycLogo from '../../assets/img/KYC_assure.png';
import HomeImage from '../../assets/img/background1.jpg';
import MobileHomeImage from '../../assets/img/mobilebackground.jpg';
import config from '../../config';

export const BackgroundImage = createGlobalStyle`
  @media screen and (max-width: 900px) { 
    body{
      background: url(${MobileHomeImage}) repeat;
      background-size: cover;
    }
  }
  body {
    background: url(${HomeImage}) repeat;
    background-size: cover;
  }
  
`;
const TITLE = 'bnbm.finance | BNB pegged algocoin'

// const BackgroundImage = createGlobalStyle`
//   body {
//     background-color: grey;
//     background-size: cover !important;
//   }
// `;

const useStyles = makeStyles((theme) => ({
  button: {
    [theme.breakpoints.down('415')]: {
      // marginTop: '10px'
    },
  },
}));

const Home = () => {
  const classes = useStyles();
  const TVL = useTotalValueLocked();
  const bnbmFtmLpStats = useLpStats('BNBM-BNB-LP');
  const carFtmLpStats = useLpStats('SBNB-BNB-LP');
  const bnbmStats = useBnbmStats();
  const carStats = useSBnbStats();
  const tBondStats = useBondStats();
  const bnbmFinance = useBnbmFinance();

  // let bnbm;
  // let sbnb;
  // if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
  //   bnbm = bnbmTesting;
  //   sbnb = carTesting;
  // } else {
  //   bnbm = bnbmProd;
  //   sbnb = carProd;
  // }

  const buyBnbmAddress =
     'https://pancakeswap.finance/swap?inputCurrency=BNB&outputCurrency=' +
    // 'https://app.bogged.finance/bsc/swap?tokenIn=BNB&tokenOut=' +
    Bnbm.address;
  // https://pancakeswap.finance/swap?outputCurrency=0x531780FAcE85306877D7e1F05d713D1B50a37F7A';
  const buyCarAddress = 'https://pancakeswap.finance/swap?inputCurrency=BNB&outputCurrency='+SBnb.address;
  const bnbmLPStats = useMemo(() => (bnbmFtmLpStats ? bnbmFtmLpStats : null), [bnbmFtmLpStats]);
  const carLPStats = useMemo(() => (carFtmLpStats ? carFtmLpStats : null), [carFtmLpStats]);
  const bnbmChart = "https://dexscreener.com/bsc/" + config.externalTokens['BNBM-BNB-LP'][0];
  const carChart = "https://dexscreener.com/bsc/" + config.externalTokens['SBNB-BNB-LP'][0];

  const bnbmPriceInDollars = useMemo(
    () => (bnbmStats ? Number(bnbmStats.priceInDollars).toFixed(2) : null),
    [bnbmStats],
  );
  const bnbmPriceInBNB = useMemo(() => (bnbmStats ? Number(bnbmStats.tokenInFtm).toFixed(4) : null), [bnbmStats]);
  const bnbmCirculatingSupply = useMemo(() => (bnbmStats ? String(bnbmStats.circulatingSupply) : null), [bnbmStats]);

  console.log('debug=> circluating supply', bnbmCirculatingSupply, bnbmPriceInDollars)
  const bnbmTotalSupply = useMemo(() => (bnbmStats ? String(bnbmStats.totalSupply) : null), [bnbmStats]);

  const carPriceInDollars = useMemo(
    () => (carStats ? Number(carStats.priceInDollars).toFixed(2) : null),
    [carStats],
  );
  const carPriceInBNB = useMemo(
    () => (carStats ? Number(carStats.tokenInFtm).toFixed(4) : null),
    [carStats],
  );
  const carCirculatingSupply = useMemo(
    () => (carStats ? String(carStats.circulatingSupply) : null),
    [carStats],
  );
  const carTotalSupply = useMemo(() => (carStats ? String(carStats.totalSupply) : null), [carStats]);

  const tBondPriceInDollars = useMemo(
    () => (tBondStats ? Number(tBondStats.priceInDollars).toFixed(2) : null),
    [tBondStats],
  );
  const tBondPriceInBNB = useMemo(() => (tBondStats ? Number(tBondStats.tokenInFtm).toFixed(4) : null), [tBondStats]);
  const tBondCirculatingSupply = useMemo(
    () => (tBondStats ? String(tBondStats.circulatingSupply) : null),
    [tBondStats],
  );
  const tBondTotalSupply = useMemo(() => (tBondStats ? String(tBondStats.totalSupply) : null), [tBondStats]);

  const bnbmLpZap = useZap({ depositTokenName: 'BNBM-BNB-LP' });
  const carLpZap = useZap({ depositTokenName: 'SBNB-BNB-LP' });

  const [onPresentBnbmZap, onDissmissBnbmZap] = useModal(
    <BNBM_BNBZapModal
      decimals={18}
      onConfirm={(zappingToken, tokenName, amount) => {
        if (Number(amount) <= 0 || isNaN(Number(amount))) return;
        bnbmLpZap.onZap(zappingToken, tokenName, amount);
        onDissmissBnbmZap();
      }}
      tokenName={'BNBM-BNB-LP'}
    />,
  );

  const [onPresentBNBMshareZap, onDissmissBNBMshareZap] = useModal(
    <ZapModal
      decimals={18}
      onConfirm={(zappingToken, tokenName, amount) => {
        if (Number(amount) <= 0 || isNaN(Number(amount))) return;
        carLpZap.onZap(zappingToken, tokenName, amount);
        onDissmissBNBMshareZap();
      }}
      tokenName={'SBNB-BNB-LP'}
    />,
  );

  return (
    <Page>
      <Helmet>
        <title>{TITLE}</title>
      </Helmet>
      <BackgroundImage />
      <Grid container spacing={3}>
        {/* Logo */}
        <Grid
          item
          xs={12}
          sm={4}
          style={{ display: 'flex', justifyContent: 'center', verticalAlign: 'middle', overflow: 'hidden' }}
        >
          <img src={BnbmImage} alt='bnbm.finance' style={{ maxHeight: '300px' }} />
        </Grid>
        {/* Explanation text */}
        <Grid item xs={12} sm={8}>
          <Paper>
            <Box p={4} style={{ textAlign: 'left' }}>
              <h3 style={{marginBottom:'10px'}}>Welcome to BNBM</h3>
              <p>
              BNBM is an algocoin which is designed to follow price of BNB. Enjoy high yields and reduce carbon footprint at the same time. 
              High yields normally only found on high risk assets, but with exposure to BNB instead! 
              BNBM is pegged via algorithm to a 100:1 ratio to BNB.<br/>
              {/* Stake your BNBM-BTC LP in the Moon to earn SBNB. Then stake your earned SBNB in the
              Heaven to earn more BNBM! */}
              </p>
              
              {/* <p>
                <a href="https://www.assuredefi.io/projects/bnbmfinance/"  className={'navLink ' + classes.link} rel="noopener noreferrer" target="_blank">
                KYC processed by
                <img alt="KYC logo" src={kycLogo} height="40px" style={{verticalAlign:"middle"}} />
              </a>
              </p> */}
            </Box>
          </Paper>
          <Grid style={{marginTop:'20px'}} item xs={12} sm={6}>
            <Card>
              <CardContent align="center">
                <h3 style={{ fontSize: '21px' }}>Total Value Locked</h3>
                <CountUp style={{ fontSize: '25px' }} end={TVL} separator="," prefix="$" />
              </CardContent>
            </Card>
          </Grid>
        </Grid>
{/* 
        <Grid container spacing={3}>
          <Grid item xs={12} sm={12} justify="center" style={{ margin: '12px', display: 'flex' }}>

            <Alert variant="filled" severity="info" style={{"background":"#250f0d"}}>
              <h3>BNBM STAKING IS LIVE!</h3>
              <h4>
                Get your xBNBM now by staking BNBM.   <Button href="/xbnbm" className="shinyButton" style={{ margin: '10px' }}>
                  Get xBNBM
                </Button>
              </h4>

            </Alert>

          </Grid>
        </Grid> */}

        {/* TVL */}
        

        {/* Wallet */}
        {/* <Grid item xs={12} sm={12}>
          <Card style={{ height: '100%' }}>
            <CardContent align="center" style={{paddingTop:'30px'}}>
              <Button color="primary" variant="contained" style={{ marginRight: '10px', marginBottom:'5px'}} className={classes.button} target="_blank" href="/heaven">Stake Now</Button>
              <Button color="secondary" variant="contained" style={{ marginRight: '10px', marginBottom:'5px' }} className={classes.button} target="_blank" href="/moon" >Farm Now</Button>
              <Button color="primary" variant="contained" style={{ marginRight: '10px', marginBottom:'5px' }} className={classes.button} target="_blank" href={buyBnbmAddress}>Buy BNBM</Button>
              <Button color="secondary" variant="contained" style={{ marginRight: '10px', marginBottom:'5px' }} className={classes.button} target="_blank" href={buyCarAddress}>Buy SBNB</Button>
              <Button color="primary" variant="contained" style={{ marginRight: '10px', marginBottom:'5px' }} target="_blank" href={bnbmChart} className={classes.button}>Bnbm chart</Button>
              <Button color="secondary" variant="contained"  target="_blank" href={carChart} className={classes.button}>SBnb chart</Button>
            </CardContent>
          </Card>
        </Grid> */}

        {/* BNBM */}
        <Grid item xs={12} sm={4}>
          <Card>
            <CardContent align="center" style={{ position: 'relative' }}>
              <Box mt={2}>
                <CardIcon>
                  <TokenSymbol symbol="BNBM" />
                </CardIcon>
              </Box>
              {/* <Button
                onClick={() => {
                  bnbmFinance.watchAssetInMetamask('BNBM');
                }}
                style={{ position: 'absolute', top: '10px', right: '10px', fontSize : '10px'}}
                color="primary" variant="contained" className={classes.button}
              >
                {' '}
                <b>+</b>&nbsp;&nbsp;
                <img alt="metamask fox" style={{ width: '20px' }} src={MetamaskFox} />
              </Button> */}
              <h3 style={{ marginBottom: '10px',
                  color: '#b025e2' }}>BNBM</h3>
              100 BNBM (1.0 Peg) =
              <Box>
                <span style={{ fontSize: '20px', color: 'white' }}>{bnbmPriceInBNB ? bnbmPriceInBNB : '-.----'} BNB</span>
              </Box>
              <Box>
                <span style={{ fontSize: '16px', alignContent: 'flex-start' }}>
                  ${bnbmPriceInDollars ? roundAndFormatNumber(bnbmPriceInDollars, 2) : '-.--'} / BNBM
                </span>
              </Box>
              <span style={{ fontSize: '12px' }}>
                Market Cap: ${roundAndFormatNumber(bnbmCirculatingSupply * bnbmPriceInDollars, 2)} <br />
                Circulating Supply: {roundAndFormatNumber(bnbmCirculatingSupply, 2)} <br />
                Total Supply: {roundAndFormatNumber(bnbmTotalSupply, 2)}
              </span>
            </CardContent>
          </Card>
        </Grid>

        {/* SBNB */}
        <Grid item xs={12} sm={4}>
          <Card>
            <CardContent align="center" style={{ position: 'relative' }}>
              {/* <Button
                onClick={() => {
                  bnbmFinance.watchAssetInMetamask('SBNB');
                }}
                style={{ position: 'absolute', top: '10px', right: '10px', fontSize : '10px'}}
                color="primary" variant="contained" className={classes.button}
              >
                {' '}
                <b>+</b>&nbsp;&nbsp;
                <img alt="metamask fox" style={{ width: '20px' }} src={MetamaskFox} />
              </Button> */}
              <Box mt={2}>
                <CardIcon>
                  <TokenSymbol symbol="SBNB" />
                </CardIcon>
              </Box>
              <h3 style={{ marginBottom: '10px',
                color: '#64a7f9'}}>SBNB</h3>
              Current Price
              <Box>
                <span style={{ fontSize: '20px', color: 'white' }}>
                  {carPriceInBNB ? carPriceInBNB : '-.----'} BNB
                </span>
              </Box>
              <Box>
                <span style={{ fontSize: '16px' }}>${carPriceInDollars ? carPriceInDollars : '-.--'} / SBNB</span>
              </Box>
              <span style={{ fontSize: '12px' }}>
                Market Cap: ${roundAndFormatNumber((carCirculatingSupply * carPriceInDollars).toFixed(2), 2)}{' '}
                <br />
                Circulating Supply: {roundAndFormatNumber(carCirculatingSupply, 2)} <br />
                Total Supply: {roundAndFormatNumber(carTotalSupply, 2)}
              </span>
            </CardContent>
          </Card>
        </Grid>

        {/* BBNB */}
        <Grid item xs={12} sm={4}>
          <Card>
            <CardContent align="center" style={{ position: 'relative' }}>
              {/* <Button
                onClick={() => {
                  bnbmFinance.watchAssetInMetamask('BBNB');
                }}
                style={{ position: 'absolute', top: '10px', right: '10px', fontSize : '10px'}}
                color="primary" variant="contained" className={classes.button}
              >
                {' '}
                <b>+</b>&nbsp;&nbsp;
                <img alt="metamask fox" style={{ width: '20px' }} src={MetamaskFox} />
              </Button> */}
              <Box mt={2}>
                <CardIcon>
                  <TokenSymbol symbol="BBNB" />
                </CardIcon>
              </Box>
              <h3 style={{ marginBottom: '10px', color : '#f401a3' }}>BBNB</h3>
              100 BBNB
              <Box>
                <span style={{ fontSize: '20px', color: 'white' }}>
                  {tBondPriceInBNB ? tBondPriceInBNB : '-.----'} BNB
                </span>
              </Box>
              <Box>
                <span style={{ fontSize: '16px' }}>${tBondPriceInDollars ? tBondPriceInDollars : '-.--'} / BBNB</span>
              </Box>
              <span style={{ fontSize: '12px' }}>
                Market Cap: ${roundAndFormatNumber((tBondCirculatingSupply * tBondPriceInDollars).toFixed(2), 2)} <br />
                Circulating Supply: {roundAndFormatNumber(tBondCirculatingSupply, 2)} <br />
                Total Supply: {roundAndFormatNumber(tBondTotalSupply, 2)}
              </span>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} sm={12}>
          <Card>
            <CardContent align="center" style={{display:'flex'}}>
              <Grid item xs={12} sm={3}>
                <CardIcon >
                  <TokenSymbol symbol="BNBM-BNB-LP" />
                </CardIcon>
              </Grid>
              <Grid item xs={12} sm={6}>
                <h3>BNBM-BNB Pancake Swap LP</h3>
                <Box mt={2} style={{margin:'0px'}}>
                  <span style={{ fontSize: '16px' }}>
                    {bnbmLPStats?.tokenAmount ? bnbmLPStats?.tokenAmount : '-.--'} BNBM /{' '}
                    {bnbmLPStats?.ftmAmount ? bnbmLPStats?.ftmAmount : '-.--'} BNB
                  </span>
                </Box>
                <Box>${bnbmLPStats?.priceOfOne ? bnbmLPStats.priceOfOne : '-.--'}</Box>
                <span style={{ fontSize: '12px' }}>
                  Liquidity: ${bnbmLPStats?.totalLiquidity ? roundAndFormatNumber(bnbmLPStats.totalLiquidity, 2) : '-.--'}{' '}
                  <br />
                  Total Supply: {bnbmLPStats?.totalSupply ? roundAndFormatNumber(bnbmLPStats.totalSupply, 2) : '-.--'}
                </span>
              </Grid>
              <Grid item xs={12} sm={3} style={{margin : 'auto'}}>
                <Button onClick={onPresentBnbmZap} disabled style={{fontSize : '16px'}} variant="contained" color="primary" className={classes.button}>
                  Zap In
                </Button>
              </Grid>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} sm={12}>
          <Card>
            <CardContent align="center" style={{display:'flex'}}>
              <Grid item xs={12} sm={3}>
                <CardIcon>
                  <TokenSymbol symbol="SBNB-BNB-LP" />
                </CardIcon>
              </Grid>
              <Grid item xs={12} sm={6}>
                <h3>SBNB-BNB Pancake Swap LP</h3>
                <Box mt={2} style={{margin:'0px'}}>
                  <span style={{ fontSize: '16px' }}>
                    {carLPStats?.tokenAmount ? carLPStats?.tokenAmount : '-.--'} SBNB /{' '}
                    {carLPStats?.ftmAmount ? carLPStats?.ftmAmount : '-.--'} BNB
                  </span>
                </Box>
                <Box>${carLPStats?.priceOfOne ? carLPStats.priceOfOne : '-.--'}</Box>
                <span style={{ fontSize: '12px' }}>
                  Liquidity: $
                  {carLPStats?.totalLiquidity ? roundAndFormatNumber(carLPStats.totalLiquidity, 2) : '-.--'}
                  <br />
                  Total Supply: {carLPStats?.totalSupply ? roundAndFormatNumber(carLPStats.totalSupply, 2) : '-.--'}
                </span>
              </Grid>
              <Grid item xs={12} sm={3} style={{margin : 'auto'}}>
                  <Button disabled onClick={onPresentBNBMshareZap} style={{fontSize : '16px'}} variant="contained" color="primary" className={classes.button}>
                    Zap In
                  </Button>
              </Grid>
            </CardContent>
          </Card>
        </Grid>
      </Grid>
            
    </Page>
  );
};

export default Home;
