import {useCallback} from 'react';
import useBnbmFinance from './useBnbmFinance';
import {Bank} from '../bnbm-finance';
import useHandleTransactionReceipt from './useHandleTransactionReceipt';

const useZap = (bank: Bank) => {
  const bnbmFinance = useBnbmFinance();
  const handleTransactionReceipt = useHandleTransactionReceipt();

  const handleZap = useCallback(
    (zappingToken: string, tokenName: string, amount: string) => {
      handleTransactionReceipt(
        bnbmFinance.zapIn(zappingToken, tokenName, amount),
        `Zap ${amount} in ${bank.depositTokenName}.`,
      );
    },
    [bank, bnbmFinance, handleTransactionReceipt],
  );
  return {onZap: handleZap};
};

export default useZap;
