import {useEffect, useState} from 'react';
import useBnbmFinance from './useBnbmFinance';
import {LPStat} from '../bnbm-finance/types';
import useRefresh from './useRefresh';

const useLpStats = (lpTicker: string) => {
  const [stat, setStat] = useState<LPStat>();
  const {slowRefresh} = useRefresh();
  const bnbmFinance = useBnbmFinance();

  useEffect(() => {
    async function fetchLpPrice() {
      try {
        setStat(await bnbmFinance.getLPStat(lpTicker));
      } catch (err) {
        console.error(err);
      }
    }
    fetchLpPrice();
  }, [setStat, bnbmFinance, slowRefresh, lpTicker]);

  return stat;
};

export default useLpStats;
