import React, {useMemo} from 'react';
import styled from 'styled-components';
import useTokenBalance from '../../hooks/useTokenBalance';
import {getDisplayBalance} from '../../utils/formatBalance';

import Label from '../Label';
import Modal, {ModalProps} from '../Modal';
import ModalTitle from '../ModalTitle';
import useBnbmFinance from '../../hooks/useBnbmFinance';
import TokenSymbol from '../TokenSymbol';
import {useMediaQuery} from '@material-ui/core';

const AccountModal: React.FC<ModalProps> = ({onDismiss}) => {
  const bnbmFinance = useBnbmFinance();

  const bnbmBalance = useTokenBalance(bnbmFinance.BNBM);
  const displayBnbmBalance = useMemo(() => getDisplayBalance(bnbmBalance, 18, 2), [bnbmBalance]);

  const carBalance = useTokenBalance(bnbmFinance.SBNB);
  const displayBNBMshareBalance = useMemo(() => getDisplayBalance(carBalance, 18, 2), [carBalance]);

  const bbondBalance = useTokenBalance(bnbmFinance.BBNB);
  const displayBbondBalance = useMemo(() => getDisplayBalance(bbondBalance, 18, 2), [bbondBalance]);

    const xbnbmBalance = useTokenBalance(bnbmFinance.XBNBM);
  const displayXbnbmBalance = useMemo(() => getDisplayBalance(xbnbmBalance, 18, 2), [xbnbmBalance]);

  const matches = useMediaQuery('(min-width:900px)');

  return (
    <Modal>
      <ModalTitle text="My Wallet" />

      <Balances style={{display: 'flex', flexDirection: matches ? 'row' : 'column'}}>
        <StyledBalanceWrapper style={{paddingBottom: '15px'}}>
          <TokenSymbol symbol="BNBM" />
          <StyledBalance>
            <StyledValue>{displayBnbmBalance}</StyledValue>
            <Label text="BNBM Available" />
          </StyledBalance>
        </StyledBalanceWrapper>

        <StyledBalanceWrapper style={{paddingBottom: '15px'}}>
          <TokenSymbol symbol="SBNB" />
          <StyledBalance>
            <StyledValue>{displayBNBMshareBalance}</StyledValue>
            <Label text="SBNB Available" />
          </StyledBalance>
        </StyledBalanceWrapper>
        {/* <StyledBalanceWrapper style={{paddingBottom: '15px'}}>
          <TokenSymbol symbol="XBNBM" />
          <StyledBalance>
            <StyledValue>{displayXbnbmBalance}</StyledValue>
            <Label text="XBNBM Available" />
          </StyledBalance>
        </StyledBalanceWrapper> */}
        <StyledBalanceWrapper style={{paddingBottom: '15px'}}>
          <TokenSymbol symbol="BBNB" />
          <StyledBalance>
            <StyledValue>{displayBbondBalance}</StyledValue>
            <Label text="BBNB Available" />
          </StyledBalance>
        </StyledBalanceWrapper>
      </Balances>
    </Modal>
  );
};

const StyledValue = styled.div`
  //color: ${(props) => props.theme.color.grey[300]};
  font-size: 30px;
  font-weight: 700;
`;

const StyledBalance = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
`;

const Balances = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: ${(props) => props.theme.spacing[4]}px;
`;

const StyledBalanceWrapper = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
  margin: 0 ${(props) => props.theme.spacing[3]}px;
`;

export default AccountModal;
