import {useEffect, useState} from 'react';
import useBnbmFinance from './useBnbmFinance';
import useRefresh from './useRefresh';

const useTotalValueLocked = () => {
  const [totalValueLocked, setTotalValueLocked] = useState<Number>(0);
  const {slowRefresh} = useRefresh();
  const bnbmFinance = useBnbmFinance();

  useEffect(() => {
    async function fetchTVL() {
      try {
        setTotalValueLocked(await bnbmFinance.getTotalValueLocked());
      } catch (err) {
        console.error(err);
      }
    }
    fetchTVL();
  }, [setTotalValueLocked, bnbmFinance, slowRefresh]);

  return totalValueLocked;
};

export default useTotalValueLocked;
