import {useEffect, useState} from 'react';
import useBnbmFinance from './useBnbmFinance';
//import {TokenStat} from '../bnbm-finance/types';
import useRefresh from './useRefresh';

const useBnbStats = () => {
  const [stat, setStat] = useState<Number>();
  const {slowRefresh} = useRefresh();
  const bnbmFinance = useBnbmFinance();

  useEffect(() => {
    async function fetchSharePrice() {
      try {
        setStat(await bnbmFinance.getBNBPriceUSD());
      } catch (err) {
        console.error(err);
      }
    }
    fetchSharePrice();
  }, [setStat, bnbmFinance, slowRefresh]);

  return stat;
};

export default useBnbStats;
