import React from 'react';

//Graveyard ecosystem logos
import bnbmLogo from '../../assets/img/bnbm.png';
import carLogo from '../../assets/img/sbnb.png';
import bnbmLogoPNG from '../../assets/img/bnbm.png';
import cabLogo from '../../assets/img/bbnb.png';

import bnbmFtmLpLogo from '../../assets/img/bnbm-bnb-lp.png';
import carFtmLpLogo from '../../assets/img/sbnb-bnb-lp.png';
import usdcLogo from '../../assets/img/USDC.png';
import bnbLogo from '../../assets/img/bnb.png';
import btcLogo from '../../assets/img/BCTB-icon.png';
import dogeLogo from '../../assets/img/DOGE-icon.png';
import ethLogo from '../../assets/img/ETH-icon.png';
import ShibaLogo from '../../assets/img/SHIBA-icon.png';
import FtmLogo from '../../assets/img/WFTM.png';
import BusdLogo from '../../assets/img/BUSD.png';
const logosBySymbol: {[title: string]: string} = {
  //Real tokens
  //=====================
  BNBM: bnbmLogo,
  BNBMPNG: bnbmLogoPNG,
  SBNB: carLogo,
  BBNB: cabLogo,
  WBNB: bnbLogo,
  BOO: bnbLogo,
  SHIB: ShibaLogo,
  ZOO: bnbLogo,
  CAKE: bnbLogo,
  SUSD: bnbLogo,
  SBTC: btcLogo,
  BTCB: btcLogo,
  BTC: btcLogo,
  SVL: bnbLogo,
  DOGE: dogeLogo,
  ETH: ethLogo,
  USDC: usdcLogo,
  FTM: FtmLogo,
  BUSD: BusdLogo,
  'BNBM-BNB-LP': bnbmFtmLpLogo,
  'BNBM-BTCB-LP': bnbmFtmLpLogo,
  'SBNB-BNB-LP': carFtmLpLogo,
  'SBNB-BNB-APELP': carFtmLpLogo,
  'BNBM-BTCB-APELP': bnbmFtmLpLogo,
};

type LogoProps = {
  symbol: string;
  size?: number;
};

const TokenSymbol: React.FC<LogoProps> = ({symbol, size = 124}) => {
  if (!logosBySymbol[symbol]) {
    throw new Error(`Invalid Token Logo symbol: ${symbol}`);
  }
  return <img src={logosBySymbol[symbol]} alt={`${symbol} Logo`} width={size} height={size} style={{margin:'auto'}} />;
};

export default TokenSymbol;
