import {useCallback, useEffect, useState} from 'react';
import {BigNumber} from 'ethers';
import ERC20 from '../bnbm-finance/ERC20';
import useBnbmFinance from './useBnbmFinance';
import config from '../config';

const useTokenBalance = (token: ERC20) => {
  const [balance, setBalance] = useState(BigNumber.from(0));
  const bnbmFinance = useBnbmFinance();
  const isUnlocked = bnbmFinance?.isUnlocked;

  const fetchBalance = useCallback(async () => {
    //console.log('=========>', token, bnbmFinance.myAccount)
    setBalance(await token.balanceOf(bnbmFinance.myAccount));
  }, [token, bnbmFinance.myAccount]);

  useEffect(() => {
    if (isUnlocked) {
      fetchBalance().catch((err) => console.error(`Failed to fetch token balance: ${err.stack}`));
      let refreshInterval = setInterval(fetchBalance, config.refreshInterval);
      return () => clearInterval(refreshInterval);
    }
  }, [isUnlocked, token, fetchBalance, bnbmFinance]);

  return balance;
};

export default useTokenBalance;
