import {useEffect, useState} from 'react';
import useBnbmFinance from './useBnbmFinance';
import {TokenStat} from '../bnbm-finance/types';
import useRefresh from './useRefresh';

const useBnbmStats = () => {
  const [stat, setStat] = useState<TokenStat>();
  const {fastRefresh} = useRefresh();
  const bnbmFinance = useBnbmFinance();

  useEffect(() => {
    async function fetchBnbmPrice() {
      try {
        setStat(await bnbmFinance.getBnbmStat());
      } catch (err) {
        console.error(err);
      }
    }
    fetchBnbmPrice();
  }, [setStat, bnbmFinance, fastRefresh]);

  return stat;
};

export default useBnbmStats;
