import {useEffect, useState} from 'react';
import useBnbmFinance from './useBnbmFinance';
import {TokenStat} from '../bnbm-finance/types';
import useRefresh from './useRefresh';

const useBondStats = () => {
  const [stat, setStat] = useState<TokenStat>();
  const {slowRefresh} = useRefresh();
  const bnbmFinance = useBnbmFinance();

  useEffect(() => {
    async function fetchBondPrice() {
      try {
        setStat(await bnbmFinance.getBondStat());
      } catch (err) {
        console.error(err);
      }
    }
    fetchBondPrice();
  }, [setStat, bnbmFinance, slowRefresh]);

  return stat;
};

export default useBondStats;
