import { useCallback, useState } from "react";
import React, {useMemo} from 'react';
import clsx from 'clsx';
import {Link} from 'react-router-dom';
import {
  AppBar,
  Box,
  Drawer,
  IconButton,
  Toolbar,
  Typography,
  useMediaQuery,
  List,
  ListItem,
  ListItemText,
  Divider,
} from '@material-ui/core';

import ListItemLink from '../ListItemLink';
import useBnbmStats from '../../hooks/useBondStats';
import useBtcStats from '../../hooks/useBtcStats';
import useBnbStats from '../../hooks/useBnbStats';
import useShareStats from '../../hooks/useSBnbStats';

import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import {makeStyles, useTheme} from '@material-ui/core/styles';
import AccountButton from './AccountButton';

import bnbmLogo from '../../assets/img/bnbmLogo_2.png';
import kycLogo from '../../assets/img/KYC_assure.png';
import {roundAndFormatNumber} from '../../0x';
//import TokenSymbol from '../TokenSymbol';

const useStyles = makeStyles((theme) => ({
  '@global': {
    ul: {
      margin: 0,
      padding: 0,
      listStyle: 'none',
    },
  },
  appBar: {
    color: '#fba919',
    background: '#000000',
    padding: '10px',
    marginBottom: '3rem',
  },
  drawer: {
    width: 240,
    flexShrink: 0,
  },
  drawerPaper: {
    width: 240,
  },
  hide: {
    display: 'none',
  },
  toolbar: {
    flexWrap: 'wrap',
  },
  toolbarTitle: {
    fontFamily: 'Rubik',
    fontSize: '0px',
    flexGrow: 1,
  },
  link: {
    textTransform: 'uppercase',
    color: '#fba919',
    fontSize: '12px',
    marginTop: '15px',
    margin: theme.spacing(10, 1, 1, 2),
    textDecoration: 'none',
    '&:hover': {
      textDecoration: 'underline',
    },
  },
  brandLink: {
    textDecoration: 'none',
    color: '#fba919',
    width: '150px',
    '&:hover': {
      textDecoration: 'none',
    },
  },
}));

const Nav = () => {

  const checkPage = useCallback((match, location, page) => {
    const currentPath = location.pathname.replace("/", "");
    if (currentPath.indexOf("board") >= 0 && page === "board") {
      return true;
    }
    if (currentPath.indexOf("tier1") >= 0 && page === "tier1") {
      return true;
    }
    if (currentPath.indexOf("tier2") >= 0 && page === "tier2") {
      return true;
    }
    if (currentPath.indexOf("tier3") >= 0 && page === "tier3") {
      return true;
    }
    return false;
  }, []);
  const matches = useMediaQuery('(min-width:900px)');
  const classes = useStyles();
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);
  const bnbmStats = useBnbmStats();
  //console.log('debug bnbmStates', bnbmStats)
  const btcStats = useBtcStats();
  const bnbStats = useBnbStats();
  const shareStats = useShareStats();

 // const [connected, setConnected] = React.useState(false);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  const btcPriceInDollars = useMemo(() => (bnbStats ? Number(bnbStats).toFixed(2) : null), [bnbStats]);
  const bnbmPriceInDollars = useMemo(
    () => (bnbmStats ? Number(bnbmStats.priceInDollars).toFixed(2) : null),
    [bnbmStats],
  );
  const sharePriceInDollars = useMemo(
    () => (shareStats ? Number(shareStats.priceInDollars).toFixed(2) : null),
    [shareStats],
  );

  return (
    <AppBar position="sticky" elevation={0} className={classes.appBar}>
      <Toolbar className={classes.toolbar}>
        {matches ? (
          <>
            <a className='bnbmlogo' href="https://bnbm.finance">
              <h1 className='bnbmlogo'>BNBMM</h1>
            </a>
            {/* <Typography variant="h6" color="inherit" noWrap style={{flexGrow: '0', fontSize: '18px'}} className={classes.toolbarTitle}>
              <a className='bnbmlogo' href="/">BNBMM</a> */}
              {/* <Link to="/" color="inherit" className={classes.brandLink}>
                <img alt="bnbm.finance" src={bnbmLogo} width="100%"/>
              </Link> */}
            {/* </Typography> */}
            <Box style={{paddingLeft: '15px', flexGrow: '1'}}>
              <Link to="/" className={'navLink ' + classes.link }>
                Board
              </Link>
              <Link to="/tier1" className={'navLink ' + classes.link}>
                Tier1
              </Link>
              <Link to="/tier2" className={'navLink ' + classes.link}>
                Tier2
              </Link>
              {/* <Link to="/xbnbm" className={'navLink ' + classes.link}>
                xBNBM
              </Link> */}
              <Link to="/tier3" className={'navLink ' + classes.link}>
                Tier3
              </Link>
              {/* <Link to="/liquidity" className={'navLink ' + classes.link}>
                Liquidity
              </Link> */}
              {/* <Link color="textPrimary" to="/sbs" className={classes.link}>
                SBS
              </Link>
              
              <Link color="textPrimary" to="/regulations" className={classes.link}>
                Regulations
              </Link> */}
              <Link to="/node" className={'navLink ' + classes.link}>
                Node
              </Link>
              <Link to="/compound" className={'navLink ' +classes.link}>
                Compound
              </Link>
              <a href="https://bnbmfinance.gitbook.io/bnbmfinance/protocol/home" className={'navLink ' + classes.link} rel="noopener noreferrer" target="_blank">
                Docs
              </a>
                {/* <a href="https://bnbm.moon" className={'navLink ' + classes.link} rel="noopener noreferrer" target="_blank">
                AutoVaults
              </a> */}
            </Box>

            <Box
              style={{
                flexGrow: '0',
                paddingLeft: '15px',
                fontSize: '1rem',
                paddingRight: '15px',
                height: '30px',
                display: 'flex',
              }}
            >
              <div className="navTokenIcon bnbm"></div>{' '}
              <div className="navTokenPrice">${roundAndFormatNumber(Number(bnbmPriceInDollars), 2)}</div>
              <div className="navTokenIcon sbnb"></div>{' '}
              <div className="navTokenPrice">${roundAndFormatNumber(Number(sharePriceInDollars), 2)}</div>
              <div className="navTokenIcon bnb"></div>{' '}
              <div className="navTokenPrice">${roundAndFormatNumber(Number(btcPriceInDollars), 2)}</div>
            </Box>
            <AccountButton text="Connect" />
          </>
        ) : (
          <>
            <IconButton
              color="inherit"
              aria-label="open drawer"
              onClick={handleDrawerOpen}
              edge="start"
              className={clsx(open && classes.hide)}
            >
              <MenuIcon />
            </IconButton>

            <Typography variant="h6" color="inherit" noWrap style={{fontSize: '18px', flexGrow: '0'}} className={classes.toolbarTitle}>
              <a className='bnbmlogo' href="/">BNBMM</a>
              {/* <Link to="/" color="inherit" className={classes.brandLink}>
                <img alt="bnbm.finance" src={bnbmLogo} width="150px" />
              </Link> */}
            </Typography>
            {/* <AccountButton text="Connect" /> */}
            <Drawer
                className={classes.drawer}
                onClose={handleDrawerClose}
              // onEscapeKeyDown={handleDrawerClose}
              // onBackdropClick={handleDrawerClose}
              variant="temporary"
              anchor="left"
              open={open}
              classes={{
                paper: classes.drawerPaper,
              }}
            >
              <div>
                <IconButton onClick={handleDrawerClose}>
                  {theme.direction === 'rtl' ? (
                    <ChevronRightIcon htmlColor="white" />
                  ) : (
                    <ChevronLeftIcon htmlColor="white" />
                  )}
                </IconButton>
              </div>
              <Divider />
              <List>
                <ListItem>
                  <AccountButton text="Connect" />
                </ListItem>
                <ListItemLink primary="Home" to="/" />
                <ListItemLink primary="Moon" to="/moon" />
                {/* <ListItemLink primary="xBNBM" to="/xbnbm" /> */}
                <ListItemLink primary="Heaven" to="/heaven" />
                <ListItemLink primary="Bank" to="/bank" />
                {/* <ListItemLink primary="SBS" to="/sbs" /> */}
                {/* <ListItemLink primary="Liquidity" to="/liquidity" /> */}
                {/* <ListItemLink primary="Regulations" to="/regulations" /> */}
                <ListItemLink primary="Node" to="/node" />
                <ListItemLink primary="Compound" to="/compound" />
                <ListItem button component="a" href="https://bnbmfinance.gitbook.io/bnbmfinance/protocol/home">
                  <ListItemText>Docs</ListItemText>
                  </ListItem>
                   {/* <ListItem button component="a" href="https://bnbm.moon">
                  <ListItemText>AutoVaults</ListItemText>
                </ListItem> */}
              </List>
            </Drawer>
          </>
        )}
      </Toolbar>
    </AppBar>
  );
};

export default Nav;
